//添加finally方法,用于指定不管 Promise 对象最后状态如何，都会执行的操作
Promise.prototype.finally = function(callback) {
	let P = this.constructor;
	return this.then(
		value => P.resolve(callback()).then(() => value),
		reason => P.resolve(callback()).then(() => {
			throw reason
		})
	);
};

let isW7 = false
let isH5 = false
const formatUrl = function(url) {
	let model_name = "longbing_massages_city";
	let baseUrl = isW7 ?
		`${siteInfo.siteroot}?i=${siteInfo.uniacid}&t=${siteInfo.multiid}&v=${siteInfo.version}&from=wxapp&c=entry&a=wxapp&do=api&core=core2&m=${model_name}&s=${url}&urls=${url}` :
		`${siteInfo.siteroot}?i=${siteInfo.uniacid}&m=${model_name}&s=${url}&urls=${url}`

	if (isH5) {
		baseUrl = `/api?i=${siteInfo.uniacid}&m=${model_name}&s=${url}`
	}
	return baseUrl
}
//阿里云地址转为本地域名的 
const formatImageUrl = function(url) {
	return url.includes(siteInfo.siteroot) ? url :
		`${formatUrl("card/getImage")}&path=${encodeURIComponent(url)}`
}

//微信小程序登录
const wxLogin = async function() {
	let {
		query
	} = await uni.getLaunchOptionsSync();
	let url = formatUrl("index/login")
	try {
		uni.showLoading({
			title: "登录中..."
		})
		let [providerErr, providerData] = await uni.getProvider({
			service: 'oauth',
		});
		let [loginErr, loginData] = await uni.login({
			provider: providerData.provider[0]
		});

		let {
			pid = 0,
				coupon_atv_id = 0
		} = query

		let login_param = {
			code: loginData.code,
			pid,
			coupon_atv_id,
		}

		// console.log("login_param==>", login_param);

		let d = await tokenFly.post(url, login_param);
		let {
			code,
			data,
			error
		} = d.data


		if (code !== 200) {
			uni.hideLoading()
			serverError({
				code,
				msg: error
			});
			throw d;
		}
		//登录成功
		uni.hideLoading()
		let {
			autograph,
			coach_status,
			data: userInfo
		} = data
		$store.commit('updateUserItem', {
			key: 'userInfo',
			val: userInfo
		})
		$store.commit('updateUserItem', {
			key: 'autograph',
			val: autograph
		})

		let {
			web_coach_port = 0
		} = $store.state.config.configInfo
		$store.commit('updateUserItem', {
			key: 'userPageType',
			val: [2, 3].includes(coach_status) && web_coach_port ? 2 : 1
		})
		return data;
	} catch (e) {
		uni.hideLoading()
		let {
			code,
			error
		} = e.response.data
		if (code !== 200) {
			serverError({
				code,
				msg: error
			});
		}
		return await Promise.reject(e);
	}
}


//公众号登录
let H5LoginNum = 0
const gzhLogin = async function() {
	let code = $util.getQueryString('code')
	let pageUrl = window.location.href
	console.log(H5LoginNum, "======H5LoginNum")
	if (H5LoginNum == 0 && code) {
		code = ''
	}
	if (code) {
		let coupon_atv_id = $util.getQueryString('coupon_atv_id') || 0
		let pid = $util.getQueryString('pid') || 0
		let url = formatUrl("index/webLogin")
		let {
			lat = 0,
				lng = 0
		} = $store.state.user.location
		try {
			let d = await tokenFly.post(url, {
				login_type: 'gzh',
				code,
				pid,
				coupon_atv_id,
				lat,
				lng
			})
			H5LoginNum = 0

			let {
				code: res_code,
				data,
				error
			} = d.data

			if (res_code !== 200) {
				uni.hideLoading()
				serverError({
					code: res_code,
					msg: error
				});
				throw d;
			}
			//登录成功
			uni.hideLoading()
			let {
				autograph,
				coach_status,
				data: userInfo
			} = data
			$store.commit('updateUserItem', {
				key: 'isGzhLogin',
				val: true
			})
			$store.commit('updateUserItem', {
				key: 'userInfo',
				val: userInfo
			})
			$store.commit('updateUserItem', {
				key: 'autograph',
				val: autograph
			})
			let {
				web_coach_port = 0
			} = $store.state.config.configInfo
			$store.commit('updateUserItem', {
				key: 'userPageType',
				val: [2, 3].includes(coach_status) && web_coach_port ? 2 : 1
			})
			return data;
		} catch (e) {
			uni.hideLoading()
			let {
				code,
				error
			} = e.response.data
			// console.log("catch e code error=======", code, error)
			if (code == 40163) {
				H5LoginNum = 0
				await $util.toAsyncLogin()
				setTimeout(() => {
					$util.hideAll()
					fly.unlock()
				}, 200)
			} else {
				serverError({
					code,
					msg: error
				});
				uni.hideLoading()
			}
			return await Promise.reject(e)
		}
	} else {
		H5LoginNum = 0
		await $util.toAsyncLogin()
		$util.hideAll()
		fly.unlock()
		return await Promise.reject("跳转授权===============")
	}
}


// app登录
let appLoginNum = 0
const appLogin = async function() {
	let {
		autograph = '',
			appLogin = {},
			loginType = 'weixin'
	} = $store.state.user

	if (appLoginNum == 0 && autograph) {
		appLogin = ''
	}
	// console.log(autograph, "=========================appLogin autograph")
	if (appLogin) {
		code = ''
		let url = formatUrl(loginType == 'weixin' ? 'index/appLogin' : 'index/iosLogin')
		try {
			let d = await tokenFly.post(url, {
				data: appLogin
			});
			appLoginNum = 0

			let {
				code: res_code,
				data,
				error
			} = d.data

			if (res_code !== 200) {
				uni.hideLoading()
				serverError({
					code: res_code,
					msg: error
				});
				throw d;
			}
			//登录成功
			uni.hideLoading()
			let {
				autograph,
				coach_status,
				data: userInfo
			} = data
			$store.commit('updateUserItem', {
				key: 'isShowLogin',
				val: false
			})
			$store.commit('updateUserItem', {
				key: 'userInfo',
				val: userInfo
			})
			$store.commit('updateUserItem', {
				key: 'autograph',
				val: autograph
			})
			let {
				web_coach_port = 0
			} = $store.state.config.configInfo
			$store.commit('updateUserItem', {
				key: 'userPageType',
				val: [2, 3].includes(coach_status) && web_coach_port ? 2 : 1
			})
			return data;
		} catch (e) {
			uni.hideLoading()
			appLoginNum = 0
			let {
				code,
				error
			} = e.response.data
			if (code !== 200) {
				serverError({
					code,
					msg: error
				});
			}
			return await Promise.reject(e);
		}

	} else {
		appLoginNum = 0
		await $util.toAsyncLogin()
		$util.hideAll()
		fly.unlock()
		return await Promise.reject("跳转授权===============");
	}
}

