//统一处理请求,satus=200网络正常code=200服务器正常
const httpType = ["post", "get"]
const formatReq = function() {
	let req = {};
	httpType.forEach((type) => {
		req[type] = async function(url, param) {
			//构造请求地址
			url = formatUrl(url);

			let res = await fly[type](url, param)
			// console.log(res, "========= formatReq res")



			let {
				code,
				error,
				data
			} = res.data
			code = code * 1
			if (code === 200) return data;
			//code!=200抛出错误
			$util.hideAll();
			if (code == 400 && error) {
				console.log(code, error, "code != 200");
				msgError({
					msg: error
				})
			} else if (code == 407) {
				$util.goUrl({
					url: `/user/pages/sys-lock`,
					openType: `reLaunch`
				})
			}
			return await Promise.reject(res.data);
		}
	})
	return req;
}
const req = formatReq();


// 定义上传,picture--代表图片 audio--音频 video--视频,默认picture
const uploadFile = async (url, {
	name = "file",
	filePath,
	contentType = '',
	header = {
		autograph: $store.state.user.autograph || '',
		isapp: isapp
	},
	formData = {
		type: 'picture'
	}
} = {}) => {
	url = formatUrl(url);
	let [resErr, res] = await uni.uploadFile({
		url,
		filePath,
		name,
		formData,
		header,
	})
	// console.log(res, "======uploadFile res")

	if (res.statusCode != 200) {
		$util.hideAll()
		console.log(res, "=====================res.statusCode != 200  networkError")
		// networkError();
		return await Promise.reject(res);
	}
	let parseData = JSON.parse(res.data)
	//服务器错误
	let {
		code,
		error,
		data
	} = parseData;
	if (code != 200) {
		$util.hideAll()
		if (code === 401) {
			fly.lock()

			console.log("==> MP-WEIXIN 401")
			await wxLogin()


























		} else {
			serverError({
				code,
				msg: error
			});
		}
		return await Promise.reject(res);
	}
	return data
}

